@include font(journalism, journalism);
@include font(kizo, kizo);
@include font(myriadpro-reg, myriadpro-regular);
@include font(myriadpro-cond, myriadpro-cond);
@include font(myriadpro-condlight, myriadpro-cond-light);
@include font(myriadpro-condbold, myriadpro-cond-bold);
@include font(roboto-condlight, roboto-cond-light);
@include font(sitka-italic, sitka-small-italic);


@mixin myriad {
    font-family: 'myriadpro-reg', sans-serif; }
@mixin myriad-c {
    font-family: 'myriadpro-cond', sans-serif; }
@mixin myriad-cl {
    font-family: 'myriadpro-condlight', sans-serif;
    font-weight: 400; }
@mixin myriad-cb {
    font-family: 'myriadpro-condbold', sans-serif; }
@mixin roboto {
    font-family: 'roboto-condlight', sans-serif; }
@mixin sitka {
    font-family: 'sitka-italic', sans-serif;
    letter-spacing: -8px;
    word-spacing: -15px; }
@mixin kizo {
    font-family: 'kizo', sans-serif; }
@mixin jour {
    font-family: 'journalism', sans-serif; }
