.start-slider {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    &__img {
    	height: 817px; }
    &__person {
        position: absolute;
        bottom: 0;
        z-index: -1; }
    &__txt {
    	width: 100%;
    	position: absolute;
    	bottom: 45px;
    	&_left {
            left: 245px;
            font-size: 80px;
            color: #000;
            line-height: 1.2;
            @include jour; }
    	&_right {
            left: 250px;
            font-size: 50px;
            color: #fff;
            @include sitka; } } }


.ba-control-handle {
    @include s(heart-large);
    @include vcenter; }
.ba-left-arrow {
    @include s(arrow-left); }
.ba-right-arrow {
    @include s(arrow-right); }

