.products {
    min-height: 905px;
    @include image('bg-products.jpg');
    @include cover;
    position: relative;
    &__img {
        width: 760px;
        height: 850px;
        @include cover;
        position: absolute;
        left: 0;
        bottom: 0;
        z-index: 0; }
    &__inner {
        width: 50%;
        min-height: inherit;
        position: absolute;
        right: 0;
        bottom: 0;
        top: 0;
        background-color: rgba(0, 0, 0, 0.5);
        padding: 65px 50px;
        z-index: 0; } }
