.upload {
    padding-top: 90px;
    padding-bottom: 60px;
    &__container {
        border-bottom: 1px solid $light;
        position: relative;
        &:before {
            content: '';
            width: 220px;
            height: 1px;
            background-color: #fff;
            display: block;
            position: absolute;
            bottom: -1px;
            left: 0; } }
    &__inner {
        padding: 80px 0;
        @include clr; }
    &__txt {
        font-size: 32px;
        text-align: center;
        @include sitka; }
    &__block {
        width: 440px;
        height: 440px;
        margin: 0 auto;
        transition: all 0.3s ease;
        cursor: pointer;
        .block__img {
            opacity: 0.5; }
        &:after {
            border: none;
            width: 0;
            height: 0; }
        &:hover {
            .block__add {
                background-color: $l-gray;
                border: 3px solid $l-gray; }
            .icon-upload {
                @include s(upload-gray); }
            .block__msg_bold {
                color: $d-coral; }
            .block__img {
                filter: grayscale(100%); } } } }
